import React, {useContext} from 'react';
import styled from 'styled-components';
import WordArt from 'react-wordart';
import Context from '../Context';

const Header = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  margin-bottom: 1.75rem;
`;

const NavigationButton = styled.button`
  filter: drop-shadow(10px 5px 4px #180096);
  height: fit-content;
  font-size: inherit;
  background: red;
  border-style: outset;
  border-color: red;
  border-width: 5px;
  text-decoration: none;
    color: blue;
    text-align: center;
    padding: 0.25rem;
    margin-left: 1.5rem;

  :hover {
    border-color: yellow;
  }

  :focus {
    border-color: yellow;
    background: yellow;
  }
`;

export default function HeaderComponent() {
    const { setSingleChannel, singleChannel,  } = useContext(Context);

    const buttonName = singleChannel ? "HOME" : "VIDEO COLLECTION"; 
  return (
        <Header>
            <WordArt text='Video-Net' theme='superhero' fontSize={80} />
            <NavigationButton onClick={() => setSingleChannel(!singleChannel)}>{buttonName}</NavigationButton>
        </Header>
  );
}
