import React from 'react';
import styled from 'styled-components';
import HomeScreen from './Components/HomeScreen';
import VideoStream from './Components/VideoStream';
import Context from './Context';
import { shuffleVideos } from './utils';

const Background = styled.div`
  display: flex;
  height: 100%;
  background-color: #2300e2;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

function App() {
  const [clips, setClips] = React.useState(shuffleVideos(10));
  const [narrative, setNarrative] = React.useState([]);
  const [singleChannel, setSingleChannel] = React.useState(false);

  const start = narrative.length < 1;
  const end = clips === narrative;

  const context = {
    clips, setClips, narrative, setNarrative, done: end, singleChannel, setSingleChannel
  };

  return (
    <Background>
      <Context.Provider value={context}>
        {singleChannel && <VideoStream/>}
        {!singleChannel && <HomeScreen/>}
      </Context.Provider>
    </Background>
  );
}

export default App;
